import React, { useEffect, useState } from "react";
import Lottie from "react-lottie";
import Header from '../header/header';
import Intro from  '../intro/intro';
import Particles from "../particles/particles";
import About from '../about/about';
import Skills from '../skills/skills';
import Resume from '../resume/resume';
import Portfolio from '../portfolio/portfolio';
import ContactUs from '../contactme/contactme';
import Footer from '../footer/footer';

import * as location from "./anim/1055-world-locations.json";

const defaultOptions1 = {
  loop: true,
  autoplay: true,
  animationData: location.default,
  rendererSettings: {
    preserveAspectRatio: "xMidYMid slice",
  },
};

function Loader() {
  const [data, setData] = useState([]);
  const [loading, setloading] = useState(undefined);
  const [completed, setcompleted] = useState(undefined);

  useEffect(() => {
    setTimeout(() => {
      fetch("https://jsonplaceholder.typicode.com/posts")
        .then((response) => response.json())
        .then((json) => {
          setData(json);

          setTimeout(() => {
            
            setcompleted(true);
          }, 1000);
        });
    }, 1500);
  }, []);

  return (
    <>
      {!completed ? (
        <>
            <Lottie style={{position: "fixed",top: "50%", left: "50%", marginTop: "-180px",marginLeft: "-150px"}} options={defaultOptions1} height={300} width={300} />
        </>
      ) : (
        <>
            <Header />
            <Intro />
            {/* <Particles />  */}
            <About />
            <Skills />
            <Resume />
            {/*<Portfolio />*/}
            <ContactUs />
            <Footer />
        </>
      )}
    </>
  );
}

export default Loader;