import React, { Component } from 'react'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import {stack as Burger} from 'react-burger-menu'

export default class header extends Component {
    constructor (props) {
    super(props)
    this.state = {
        menuOpen: false
    }
    }

    // This keeps your state in sync with the opening/closing of the menu
    // via the default means, e.g. clicking the X, pressing the ESC key etc.
    handleStateChange (state) {
    this.setState({menuOpen: state.isOpen})  
    }
    
    // This can be used to close the menu, e.g. when a user clicks a menu item
    closeMenu () {
    this.setState({menuOpen: false})
    }

    // This can be used to toggle the menu, e.g. when using a custom icon
    // Tip: You probably want to hide either/both default icons if using a custom icon
    // See https://github.com/negomi/react-burger-menu#custom-icons
    toggleMenu () {
    this.setState(state => ({menuOpen: !state.menuOpen}))
    }

    render(){
        return(
            <header className="s-header">
                <div className="row s-header__nav-wrap">
                <nav className="s-header__nav">
                    <ul>
                        <li><AnchorLink href='#about'>About</AnchorLink></li>
                        <li><AnchorLink href='#resume'>Experience</AnchorLink></li>
                        <li><AnchorLink href='#skills'>Skills</AnchorLink></li>
                        <li><AnchorLink href='#education'>Education</AnchorLink></li>
                        <li><AnchorLink href='#contact'>Say Hello</AnchorLink></li>
                    </ul>
                </nav>
                </div>
                <div>
                <Burger isOpen={this.state.menuOpen} onStateChange={(state) => this.handleStateChange(state)} 
                        left pageWrapId={"intro"} outerContainerId={"app"}>
                    <AnchorLink onClick={() => this.closeMenu()} href='#about'>About</AnchorLink>
                    <AnchorLink onClick={() => this.closeMenu()} href='#resume'>Experience</AnchorLink>
                    <AnchorLink onClick={() => this.closeMenu()} href='#skills'>Skills</AnchorLink>
                    <AnchorLink onClick={() => this.closeMenu()} href='#education'>Education</AnchorLink>
                    <AnchorLink onClick={() => this.closeMenu()} href='#contact'>Say Hello</AnchorLink>
                </Burger>
                </div>
          </header>
        )
    }
}